<template>
  <div class="class-info">
    <h1> &nbsp; 2012 &nbsp;1201</h1>
    <h2></h2>
    <table>
      <tr>
        <td></td>
        <td></td>
        <td></td>
        <td>QQ</td>
        <td></td>
      </tr>
        <tr v-for="man in contacts">
            <td>{{man.role}}</td>
            <td>{{man.name}}</td>
            <td>{{man.phonenum}}</td>
            <td>{{man.qq}}</td>
            <td>{{man.dorm}}</td>
        </tr>
    </table>
    <h2></h2>
    <table>
      <tr>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
        <td></td>
      </tr>
      <tr>
        <td></td>
        <td>{{deals[0].vomule}}</td>
        <td>{{deals[1].vomule}}</td>
        <td>{{deals[2].vomule}}</td>
        <td>{{deals[3].vomule}}</td>
      </tr>
        <tr>
            <td></td>
            <td @click="()=>location.hash = '#/class/bargin/'+this.$route.params.id+'/PAPER'">+</td>
            <td @click="()=>location.hash = '#/class/bargin/'+this.$route.params.id+'/D_SCHOOL'">+</td>
            <td @click="()=>location.hash = '#/class/bargin/'+this.$route.params.id+'/MEETING'">+</td>
            <td @click="()=>location.hash = '#/class/bargin/'+this.$route.params.id+'/T-SHIRT'">+</td>
        </tr>
    </table>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'classInfo',
  data () {
    let __this = this;

    axios.post("/job/getAllAtten?marketid="+__this.$route.params.id)
      .then(function (res) {
        if(res.data.msg === "SUCCESS")
          __this.contacts = res.data.result;
      })
      .catch(function (e) {
        alert("")
      })

    axios.get("/job/getAllDeal?marketid="+__this.$route.params.id)
      .then(function (res) {
        if(res.data.msg === "SUCCESS")
          __this.deals = res.data.result;
      })
      .catch(function (e) {
        alert("")
      })

    return {
      deals:[{vomule:"loading"},{vomule:"loading"},{vomule:"loading"},{vomule:"loading"}],
      contacts:[{role:"loading"}],
      location:location
    }
  }
}
</script>

<style scoped>
.class-info>h1{
  font-size: 1.2rem;
  font-weight: lighter;
}
.class-info>h2{
  font-size: 0.8rem;
  font-weight: lighter;
}
.class-info>table{
  width: 100%;
}
.class-info>table td{
  border: 1px #ddd solid;
  font-size: 0.8rem;
}
</style>



